import create from '.Object.create?=';

var NEED_TO_ESCAPE_IN_REGEXP = /^[$()*+\-.?[\\\]^{|]$/;
var SURROGATE_PAIR = /^[\uD800-\uDBFF][\uDC00-\uDFFF]/;
var GROUP :Group = create(null);

export default function groupify (branches :string[], uFlag? :boolean, noEscape? :boolean) :string {
	var group :Group = create(null);
	var appendBranch = uFlag ? appendPointBranch : appendCodeBranch;
	for ( var length :number = branches.length, index :number = 0; index<length; ++index ) { appendBranch(group, branches[index]); }
	return sourcify(group, !noEscape);
};

function appendPointBranch (group :Group, branch :string) :void {
	if ( branch ) {
		var char :string = SURROGATE_PAIR.test(branch) ? branch.slice(0, 2) : branch.charAt(0);
		appendPointBranch(group[char] || ( group[char] = create(null) ), branch.slice(char.length));
	}
	else { group[''] = GROUP; }
}

function appendCodeBranch (group :Group, branch :string) :void {
	if ( branch ) {
		var char :string = branch.charAt(0);
		appendCodeBranch(group[char] || ( group[char] = create(null) ), branch.slice(1));
	}
	else { group[''] = GROUP; }
}

function sourcify (group :Group, needEscape :boolean) :string {
	var branches :string[] = [];
	var singleCharactersBranch :string[] = [];
	var noEmptyBranch :boolean = true;
	for ( var char in group ) {
		if ( char ) {
			var sub_branches :string = sourcify(group[char], needEscape);
			if ( needEscape && NEED_TO_ESCAPE_IN_REGEXP.test(char) ) { char = '\\'+char; }
			sub_branches ? branches.push(char+sub_branches) : singleCharactersBranch.push(char);
		}
		else { noEmptyBranch = false; }
	}
	singleCharactersBranch.length && branches.unshift(singleCharactersBranch.length===1 ? singleCharactersBranch[0] : '['+singleCharactersBranch.join('')+']');
	return branches.length===0
		? ''
		: ( branches.length===1 && ( singleCharactersBranch.length || noEmptyBranch )
			? branches[0]
			: '(?:'+branches.join('|')+')'
		)
		+( noEmptyBranch ? '' : '?' );
}

type Group = { [char :string] :Group };
